/* src/App.css */

body {
  margin: 0;
  border: 0;
  display: flexbox;
  overflow: hidden;
}

.background-video {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.app-container {
  text-align: center;
}

.logo-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

.logo {
  width: 70px;
  height: 70px;
  margin-right: 10px;
}

h1 {
  cursor: pointer;
  position: relative;
  padding: 10px 20px;
  background: white;
  font-size: 28px;
  border-top-right-radius: 10px;
  border-bottom-left-radius: 10px;
  transition: all 1s;
}

h1:after,
h1:before {
  content: " ";
  width: 10px;
  height: 10px;
  position: absolute;
  border: 0px solid #fff;
  transition: all 1s;
}

h1:after {
  top: -1px;
  left: -1px;
  border-top: 5px solid black;
  border-left: 5px solid black;
}

h1:before {
  bottom: -1px;
  right: -1px;
  border-bottom: 5px solid black;
  border-right: 5px solid black;
}

h1:hover {
  border-top-right-radius: 0px;
  border-bottom-left-radius: 0px;
}

h1:hover:before,
h1:hover:after {
  width: 100%;
  height: 100%;
}

.table-container {
  width: 60%;
}

.center-container {
  display: flex;
  flex-direction: column;
}

.center-section {
  margin: 10px;
}

.black-textbox {
  width: 280px;
  height: 100px;
  padding: 5px;
  margin-left: 50px;
  margin-bottom: 20px;
  background-color: #e3e4fa;
  color: #000000;
  border: 1px solid #fff;
  border-radius: 4px;
  outline: none;
}

.remaining-buttons {
  display: flex;
  justify-content: space-around;
  margin: 20px;
}

.med-button {
  padding: 10px;
  margin: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.5s ease;
}

.med-button:hover {
  background-color: #ffffff;
  color: #000;
  border-radius: 10px;
}

.record-prescription {
  background: #123456;
  color: #ffffff;
  transition: all 0.5s ease-in-out;
}

.record-prescription:hover {
  background-color: #ffffff;
  color: #000;
  border-radius: 10px;
  border: 3px solid #123456;
}

.record-precaution {
  background: #123456;
  margin-right: 52px;
  color: #ffffff;
  transition: all 0.5s ease-in-out;
}

.record-precaution:hover {
  background-color: #ffffff;
  color: #000;
  border-radius: 10px;
  border: 3px solid #123456;
}

.stop-recording {
  background: rgb(255, 0, 0);
  color: #ffffff;
  transition: all 0.5s ease-in-out;
}

.stop-recording:hover {
  background-color: #ffffff;
  color: #000;
  border-radius: 10px;
  border: 3px solid #000;
}

#patient-details {
  background: #12e193;
  color: #ffffff;
  transition: all 0.5s ease-in-out;
}

#patient-details:hover {
  background-color: #ffffff;
  color: #000;
  border-radius: 10px;
  border: 3px solid #12e193;
}

#view-transcript {
  background: #438d80;
  color: #ffffff;
  transition: all 0.5s ease-in-out;
}

#view-transcript:hover {
  background-color: #ffffff;
  color: #000000;
  border-radius: 10px;
  border: 3px solid #438d80;
}

#send-to-patient {
  background: #6cbb3c;
  transition: all 0.5s ease-in-out;
  color: #ffffff;
}

#send-to-patient:hover {
  background-color: #ffffff;
  color: #000;
  border-radius: 10px;
  border: 3px solid #6cbb3c;
}

.gif-animation {
  margin-top: 20px;
  max-width: 300px;
  margin-left: auto;
  margin-right: auto;
}

.gif-animation img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease-in-out;
}

.gif-animation img:hover {
  transform: scale(1.05);
}
